import { createClient } from 'backendless-console-sdk'
import axios from 'axios'

function createApiClient() {
  return createClient(
    'https://develop.backendless.com',
    'dhichpbxgjbocqmsogrireonvqnlqfdykqyo'
  )
}

async function getDataTables(apiClient) {
  const tablesData = await apiClient.tables.get(
    '24791ABF-AE63-D664-FF20-E4CDD97BD400'
  )

  const tables = tablesData.tables

  return tables
    .filter(({ system }) => !system)
    .map(({ name, columns, relations, parentRelations }) => {
      if (parentRelations) {
        parentRelations = parentRelations.map(({ name, relatedColumnName }) => {
          const [tableName, columnName] = name
            .replace('__related__', '')
            .split('.')

          return {
            relatedColumnName,
            tableName,
            columnName,
          }
        })
      }

      return {
        name,
        columns,
        relations,
        parentRelations,
      }
    })
}

