function createFunctionObjects(tables) {
  const dynamicFunctionStorage = []

  for (const table of tables) {
    if (!table.parentRelations) continue

    for (const parentRelation of table.parentRelations) {
      dynamicFunctionStorage.push({
        table: table,
        name: `get_${table.name}_by_${parentRelation.tableName}`,
        function: function (parentObjectId) {
          return {
            sqlSearch: true,
            where: `${parentRelation.relatedColumnName} in (${parentRelation.tableName}[objectId = '${parentObjectId}'].${parentRelation.columnName}.${parentRelation.relatedColumnName})`,
          }
        },
        description: `Get ${table.name} records by ${parentRelation.tableName} objectId`, // TODO: change objectid to dynamic field
        parameters: {
          type: 'object',
          properties: {
            // [`${parentRelation.tableName}_${parentRelation.relatedColumnName}`]: {
            //   type: 'string', //TODO change to type of parentRelation.relatedColumnName
            //   description: `${parentRelation.tableName} ${parentRelation.relatedColumnName}`
            // }
          },
          required: [
            `${parentRelation.tableName}_${parentRelation.relatedColumnName}`,
          ],
        },
      })
    }
  }

  return dynamicFunctionStorage
}

function clearFunctionObjects(generatedFunctions) {
  return generatedFunctions.map(func => {
    return {
      description: func.description,
      name: func.name,
      parameters: func.parameters
    }
  })
}

async function getAIKey() {
  const keys = await axios.get(
    'https://steamyjail.backendless.app/api/services/Test/chatgptKey'
  )

  return keys.data.aiKey
}


