import { useState, useEffect } from 'react'
import {
  createFunctionObjects,
  getDataTables,
  createApiClient,
  clearFunctionObjects,
  getAIKey,
} from './logic'
import axios from 'axios'
import './App.css'

const BC_KEY = 'dhichpbxgjbocqmsogrireonvqnlqfdykqyo'
const APP_ID = '24791ABF-AE63-D664-FF20-E4CDD97BD400'


function findGeneratedFunctionToCall(functions, name) {
  for (const func of functions) {
    if (func.name === name) return func
  }
}

function App() {
  const [message, setMessage] = useState('')
  const [chats, setChats] = useState([])
  const [isTyping, setIsTyping] = useState(false)
  const [generatedFuntionObjects, setGeneratedFuntionObjects] = useState([])
  const [AIFunctionObjects, setAIFunctionObjects] = useState([])
  const [aiKey, setAiKey] = useState('')



  useEffect(() => {
    const createAIFunctions = async () => {
      const aiKey = await getAIKey()

      setAiKey(aiKey)

      const apiClient = createApiClient()

      const dataTables = await getDataTables(apiClient)

      const AIFunctionObjects = createFunctionObjects(dataTables)

      setGeneratedFuntionObjects(AIFunctionObjects)

      const aiFunc = clearFunctionObjects(AIFunctionObjects)

      setAIFunctionObjects(aiFunc)
    }
    createAIFunctions()
  }, [])




  const chat = async (e, message) => {
    e.preventDefault()

    if (!message) return
    setIsTyping(true)
    // scrollTo(0, 1e10)

    let msgs = chats
    msgs.push({ role: 'user', content: message })
    setChats(msgs)
    setMessage('')

    const aiResponse = await axios.post(
      'https://api.openai.com/v1/chat/completions',
      {
        model: 'gpt-3.5-turbo-0613',
        messages: chats,
        functions: AIFunctionObjects,
        function_call: 'auto',
      },
      {
        headers: {
          Authorization: `Bearer ${aiKey}`,
          'Content-Type': 'application/json',
        },
      }
    )

    const responseMessage = aiResponse.data.choices[0].message

    if (responseMessage.function_call) {
      let tmpFuncMsgs = [...msgs]

      const functionName = responseMessage.function_call.name

      const functionToCall = findGeneratedFunctionToCall(
        generatedFuntionObjects,
        functionName
      )
      const dbQuery = functionToCall.function(
        'B36E2D4C-89F4-4195-BDBE-12E03A7FACEC'
      )
      const table = functionToCall.table
      // const functionArgs = JSON.parse(responseMessage.function_call.arguments);
      const apiClient = createApiClient()
      const functionResponse = await apiClient.tables.loadRecords(
        APP_ID,
        table,
        dbQuery
      )
      
      tmpFuncMsgs.push(responseMessage)
      tmpFuncMsgs.push({
        role: 'function',
        name: functionName,
        content: JSON.stringify(functionResponse),
      })

      const second_response = await axios.post(
        'https://api.openai.com/v1/chat/completions',
        {
          model: 'gpt-3.5-turbo-0613',
          messages: tmpFuncMsgs,
        },
        {
          headers: {
            Authorization: `Bearer ${aiKey}`,
            'Content-Type': 'application/json',
          },
        }
      )
      msgs.push(second_response.data.choices[0].message)
    } else {
      msgs.push(responseMessage)
    }
    setChats(msgs)
    setIsTyping(false)
  }

  return (
    <main>
      <h1>OpenAI support bot</h1>

      <section>
        {chats && chats.length
          ? chats.map((chat, index) => (
              <p key={index} className={chat.role === 'user' ? 'user_msg' : ''}>
                <span>
                  <b>{chat.role.toUpperCase()}</b>
                </span>
                <span>:</span>
                <span>{chat.content}</span>
              </p>
            ))
          : ''}
      </section>

      <div className={isTyping ? '' : 'hide'}>
        <p>
          <i>{isTyping ? 'Typing' : ''}</i>
        </p>
      </div>

      <form action="" onSubmit={(e) => chat(e, message)}>
        <input
          type="text"
          name="message"
          value={message}
          placeholder="Type a message here and hit Enter..."
          onChange={(e) => setMessage(e.target.value)}
        />
      </form>
    </main>
  )
}

export default App
